<script>
  import {
    StatsCard,
    ChartCard,
    NavTabsCard,
    NavTabsTable,
    OrderedTable,
    SimpleTable
  
  } from "@/components";
  
  import axios from 'axios';
  
  export default {
    props: {
      tableHeaderColor: {
        type: String,
        default: ""
      }
    },
    components: {
      StatsCard,
      ChartCard,
      NavTabsCard,
      NavTabsTable,
      OrderedTable,
      SimpleTable
    },
  
    data() {
      return {
        title_form: null,
      location_form: null,
      description_form: null,
      status_form: null,
      price_form: null,   

        location: null,
        status: null,
        properties: [],
  
        dailySalesChart: {
          data: {
            labels: ["M", "T", "W", "T", "F", "S", "S"],
            series: [
              [12, 17, 7, 17, 23, 18, 38]
            ]
          },
          options: {
            lineSmooth: this.$Chartist.Interpolation.cardinal({
              tension: 0
            }),
            low: 0,
            high: 50, // creative tim: we recommend you to set the high sa the biggest value + something for a better look
            chartPadding: {
              top: 0,
              right: 0,
              bottom: 0,
              left: 0
            }
          }
        },
        dataCompletedTasksChart: {
          data: {
            labels: ["12am", "3pm", "6pm", "9pm", "12pm", "3am", "6am", "9am"],
            series: [
              [230, 750, 450, 300, 280, 240, 200, 190]
            ]
          },
  
          options: {
            lineSmooth: this.$Chartist.Interpolation.cardinal({
              tension: 0
            }),
            low: 0,
            high: 1000, // creative tim: we recommend you to set the high sa the biggest value + something for a better look
            chartPadding: {
              top: 0,
              right: 0,
              bottom: 0,
              left: 0
            }
          }
        },
        emailsSubscriptionChart: {
          data: {
            labels: [
              "Ja",
              "Fe",
              "Ma",
              "Ap",
              "Mai",
              "Ju",
              "Jul",
              "Au",
              "Se",
              "Oc",
              "No",
              "De"
            ],
            series: [
              [542, 443, 320, 780, 553, 453, 326, 434, 568, 610, 756, 895]
            ]
          },
          options: {
            axisX: {
              showGrid: false
            },
            low: 0,
            high: 1000,
            chartPadding: {
              top: 0,
              right: 5,
              bottom: 0,
              left: 0
            }
          },
          responsiveOptions: [
            [
              "screen and (max-width: 640px)",
              {
                seriesBarDistance: 5,
                axisX: {
                  labelInterpolationFnc: function(value) {
                    return value[0];
                  }
                }
              }
            ]
          ]
        }
      };
    },
    methods: {
      clear() {
        this.location = null
        this.status = null
      },
  
      getProperties() {
        const path = 'http://vligas.pythonanywhere.com/get_properties';
        axios.post(path, {
          location: this.location,
          status: this.status
        }).then((res) => {
          let temp = res.data.properties  
          if(temp != null) {
             temp.forEach(propertie => {
               let string_status = ''
              switch (propertie.status) {
                case 0:
                  string_status = "En venta"
                  break;
                case 1:
                  string_status = "En alquiler"
                  break;
                case 2:
                  string_status = "Vendida"
                  break;
                case 3:
                  string_status = "Alquilada"
                  break;              
              }
              propertie.string_status = string_status
            });

            this.properties = temp
            console.log("TABLE: ", this.properties)
            console.log("this.location: ", this.location)
            console.log("this.status: ", this.status)
          }

        }).catch((err) => {
          console.log(err)
        })
      },
    },
    created() {
      this.getProperties()
    }
  };
</script>

