        title_form: null,
        location_form: null,
        description_form: null,
        status_form: null,
        price_form: null,

        title_edit: null,
        location_edit: null,
        description_edit: null,
        status_edit: null,
        price_edit: null,
        id_edit: null,
        potencial_client_id_edit: null,
        
  
        showDialog: false,
        editDialog: false,
  
        location: null,
        status: null,
        properties: [],
        selected: [],
      }
    },
    methods: {
      addPropertie() {
        console.log("Titulo: ", this.title_form)
        console.log("localidad: ", this.location_form)
        console.log("descripcion: ", this.description_form)
        console.log("estado: ", this.status_form)
        console.log("precio: ", this.price_form)
        this.showDialog = false
  
        if (this.title_form !== null && this.location_form !== null && this.description_form !== null && this.status_form !== null && this.price_form !== null) {
          const path = 'http://vligas.pythonanywhere.com/add_propertie';
          axios.post(path, {
            potencial_client_id: -1,
            description: this.description_form,
            title: this.title_form,
            location: this.location_form,
            status: this.status_form,
            price: this.price_form,
          }).then((res) => {
  
            console.log(res)
            this.getProperties()
  
            this.description_form = null
            this.title_form = null
            this.location_form = null
            this.status_form = null
            this.price_form = null
  
          }).catch((err) => {
            console.log(err)
          })
        } else {
          console.log("No puedo agregar")
        }
      },

      updatePropertie() {
        console.log("id: ", this.id_edit)
        console.log("this.title_edit: ", this.title_edit)
        console.log("this.location_edit: ", this.location_edit)
        console.log("this.description_edit: ", this.location_edit)
        console.log("this.status_edit: ", this.status_edit)
        console.log("this.price_edit: ", this.price_edit)

          const path = 'http://vligas.pythonanywhere.com/update_propertie';
            axios.post(path, {
              propertie_id: this.id_edit,
              description: this.description_edit,
              title: this.title_edit,
              location: this.location_edit,
              status: this.status_edit,
              price: this.price_edit
            }).then((res) => {
              console.log("EDIT")              
              this.getProperties()
              this.editDialog = false
            }).catch((err) => {
              console.log(err)
            })

            this.id_edit = null
            this.description_edit = null
            this.title_edit = null
            this.location_edit = null
            this.status_edit = null
            this.price_edit = null
          
      },

      editPropertie() {
        console.log("selected: ", this.selected)
        console.log("THIS STATUS: ", this.selected[0].status)
        console.log("this.selected.length: ", this.selected.length)
  
  
        if (this.selected.length === 1) {
          
          this.editDialog = true
          this.id_edit = this.selected[0].id
          this.potencial_client_id_edit = this.selected[0].potencial_client_id
        }
      },
  
      deleteProperties() {
        console.log("selected: ", this.selected)
        console.log("THIS STATUS: ", this.selected[0].status)
        console.log("this.selected.length: ", this.selected.length)
        let proceedToDelete = true
        let i = 0
        while (proceedToDelete && i < this.selected.length) {
          if (this.selected[i].status == 0 && this.selected[i].status == 1)
            proceedToDelete = false
          i += 1
        }
  
        if (proceedToDelete) {
          const path = 'http://vligas.pythonanywhere.com/delete_properties';
          axios.post(path, {
            selected: this.selected
          }).then((res) => {
            console.log("DELETE")
            this.getProperties()
            // this.properties = res.data.properties;
            // console.log("TABLE: ", this.properties)
            // console.log("this.location: ", this.location)
            // console.log("this.status: ", this.status)
  
          }).catch((err) => {
            console.log(err)
          })
        } else {
          console.log("No puedo eliminar porque esta en alquiler o en venta")
        }
  
  
      },
  
      clear() {
        this.location = null
        this.status = null
      },
  
      getProperties() {
        const path = 'http://vligas.pythonanywhere.com/get_properties';
        axios.post(path, {
          location: this.location,
          status: this.status
        }).then((res) => {
          let temp = res.data.properties
          if (temp != null) {
            temp.forEach(propertie => {
              let string_status = ''
              switch (propertie.status) {
                case 0:
                  string_status = "En venta"
                  break;
                case 1:
                  string_status = "En alquiler"
                  break;
                case 2:
                  string_status = "Vendida"
                  break;
                case 3:
                  string_status = "Alquilada"
                  break;
              }
              propertie.string_status = string_status
            });
  
            this.properties = temp
            console.log("TABLE: ", this.properties)
            console.log("this.location: ", this.location)
            console.log("this.status: ", this.status)
          }
  
        }).catch((err) => {
          console.log(err)
        })
      },
  
      onSelect(items) {
        this.selected = items
        console.log("THISSSSSSSS selected: ", this.selected)
      },
  
      getAlternateLabel(count) {
        let to_show = ''
  
        if (count > 1) {
          to_show = `${count} propiedades seleccionadas`
        } else
          to_show = `${count} propiedad seleccionada`
  
        return to_show
      }
    },
    created() {
      this.getProperties()
    }
  };
